
.paginator {
    display: flex;
    align-items: center;
    padding-left: 20px;
    column-gap: 5px;
}

.buttonPrev {
    visibility: hidden;
}
.buttonPrevActive {
    visibility: visible;
}

.body {}

.buttonNext {}

.item {
    transition: all 0.3s ease 0s;
    @media (any-hover: hover) {
        &:hover {
            font-weight: bold;  
        }
      }          
        
}

.selected {
    font-weight: bold;  
}